import * as PropTypes from "prop-types"
import React from "react"
import Img from "gatsby-image";
import { graphql } from 'gatsby';

class Card extends React.Component {
  static propTypes = {
    card: PropTypes.shape({
      title: PropTypes.string.isRequired,
      url: PropTypes.string.isRequired,
      description: PropTypes.string.isRequired,
      image: PropTypes.object,
    }).isRequired,
  }

  render() {
    const { title, url, description, image } = this.props.card

    return (
      <a
      href={url}
      className="card"
      target="_blank" 
      rel="noopener noreferrer"
    >
      <div className="card-context">
      <Img fluid={image.childImageSharp.fluid} alt="" className="card-image" />
      </div>

      <div className="card-lens">
        <h2>
          {title}
        </h2>
        <p>
          {description}
        </p>
      </div>

    </a>
    )
  }
}

export default Card

export const CardFragment = graphql`
  fragment Card_details on LinksJson {
    links {
      title
      url
      description
      image {
        childImageSharp {
          fluid(maxWidth: 384, maxHeight: 200) {
            ...GatsbyImageSharpFluid_noBase64
          }
        }
      }
    }
  }
`
